@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap");

.btn-group {
  width: 8vw;
  height: 6vh;
  border: 3px solid hsl(214, 90%, 23%);
  background-color: hsl(214, 100%, 70%);
  color: #ffffff;
  font-size: 1.9vmin;
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: lighter;
  cursor: pointer;
  display: block;
  margin-right: 15px;
  margin-bottom: -3px;
  text-align: center;
  transition: all 0.2s ease;
  margin-top: -2.7px;
}

.btn-group:hover {
  background-color: hsl(214, 51%, 61%);
  transform: scale(1.08);
  transition: all 0.2s ease;
  color: rgb(225, 226, 159);
}

.container {
  display: flex;
  flex-direction: row;
  padding: 15px 0px 10px 15px;
  justify-content: left;
}

.title-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: solid black;
  background-color: hsl(220, 13%, 18%);
  padding: 0px 0px 0px 0px;
  height: 60px;
}

.title {
  width: 17vw;
  display: block;
  height: 50px;
  font-size: 1.7vw;
  text-align: center;
  text-transform: uppercase;
  color: hsl(214, 100%, 70%);
  /* text-decoration: underline;
  text-decoration-color: white;
  text-decoration-thickness: 1px;
  text-decoration-style: solid; */
  text-align: center;
  font-family: "Open Sans Condensed", sans-serif;
  cursor: pointer;
  background-color: hsl(220, 13%, 18%);
  border-bottom: 1.8px solid white;
  margin-left: 7px;
  margin-top: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title-button-row {
  width: 100%;
  align-items: flex-start;
  display: flex;
}

.title-button {
  width: fit-content;
  height: 4.5vh;
  padding-left: 1vw;
  padding-right: 1vw;
  border: 3px solid hsl(214, 90%, 23%);
  background-color: hsl(214, 100%, 70%);
  color: #ffffff;
  font-size: 1vw;
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: lighter;
  cursor: pointer;
  margin-right: 10px;
  margin-left: 10px;
  text-align: center;
  transition: all 0.2s ease;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box;
  display: inline-block;
}

.title-button:hover {
  background-color: hsl(214, 51%, 61%);
  transform: scale(1.08);
  transition: all 0.2s ease;
  color: rgb(225, 226, 159);
}

.info-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 3px solid hsl(214, 90%, 23%);
  background-color: hsl(214, 100%, 70%);
  color: #ffffff;
  font-size: 20px;
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: lighter;
  cursor: pointer;
  margin-right: 10px;
  margin-left: 10px;
  text-align: center;
  transition: all 0.2s ease;
}

.info-button:hover {
  background-color: hsl(214, 51%, 61%);
  transform: scale(1.08);
  transition: all 0.2s ease;
  color: rgb(225, 226, 159);
  border-radius: 50%;
}

.algo-btn-group {
  width: 3.5vmin;
  height: 3.5vmin;
  border: 3px solid hsl(214, 90%, 23%);
  background-color: hsl(214, 100%, 70%);
  color: #ffffff;
  font-size: 2.2vmin;
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: 900;
  cursor: pointer;
  display: block;
  margin-left: -3px;
  margin-right: 10px;
  text-align: center;
  transition: all 0.2s ease;
  border-radius: 50%;
  padding: 0px;
  -webkit-padding-start: 0px;
}

.algo-btn-group:hover {
  background-color: hsl(214, 51%, 61%);
  transform: scale(1.08);
  transition: all 0.2s ease;
  color: rgb(225, 226, 159);
}
